@media screen and (max-width: 540px) {
  html {
    font-size: 14px; } }

.container {
  padding-top: 1em; }

.react-select {
  flex-grow: 1; }

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.table tr:nth-of-type(n+2) td {
  font-size: 0.7em;
  vertical-align: middle;
  text-align: center; }

.table tr td.bg-green, .table tr td.checked {
  background: #28a745; }

.table tr td.bg-grey, .table tr td.todo {
  background-color: #a0a4ab; }

.table tr td.bg-yellow, .table tr td.in_progress {
  background-color: #ffc107; }

.table tr td.bg-pink, .table tr td.checking {
  background-color: #ed9ba3; }

.table tr td.bg-red, .table tr td.no-mark {
  background-color: #dc3545; }

