.table {
  tr {

    &:nth-of-type(n+2) {
      td {
        font-size: 0.7em;
        vertical-align: middle;
        text-align: center;
      }
    }
    
    td {

      &.bg-green,
      &.checked {
        background: #28a745;
      }

      &.bg-grey,
      &.todo {
        background-color: darken(#d6d8db, 20%);
      }

      &.bg-yellow,
      &.in_progress {
        background-color: #ffc107;
      }

      &.bg-pink,
      &.checking {
        background-color: darken(#f5c6cb, 10%);
      }

      &.bg-red,
      &.no-mark {
        background-color: #dc3545;
      }
    }
  }
}
